.city-popup {
  &__part1 {
    display: block;
  }

  &__part2 {
    display: none;
  }

  &__inner{
    position: relative;
  }

  &__input{
    background: inherit;
    border: 2px solid $colorWhite;
    height: 57px;
    border-radius: 10px;
    color: $colorWhite;
    text-transform: uppercase;
    width: 100%;
    padding: 10px 10px 5px 45px;
  }

  &__input:focus{
    outline: none;
  }

  &__buttons{
    display: flex;
    justify-content: space-between;
    margin-bottom: 22px;
    margin-top: 22px;
  }

  &__button{
    background: $colorWhite;
    color: $colorPrimary;
    text-transform: uppercase;
    font-size: 18px;
    border: 2px solid $colorWhite;
    height: 55px;
    border-radius: 10px;
    padding-top:7px;
  }

  &__button1{
    width: 84px;
  }

  &__result{
    margin-top: 5px;
    border: 2px solid $colorWhite;
    border-radius: 10px;
    padding: 25px 47px 5px 47px;
    overflow: scroll;
    display:none;
    &.m-open{
      display: block;
    }
  }

  &__city {
    padding-bottom: 20px;
  }

  &__icon-loc {
    cursor: pointer;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 20px;
    height: 22px;
    width: 22px;
    padding: 0;
    background: url(/img/loc-popup.svg) no-repeat;
    background-size: 22px 22px;
    border: none;
  }

  &__icon-search {
    cursor: pointer;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 20px;
    height: 22px;
    width: 22px;
    padding: 0;
    background: url(/img/search-popup.svg) no-repeat;
    background-size: 22px 22px;
    border: none;
  }
}